import chaobject from "@/components/script/chaobject";

export default {
  name: "cha_check_item",
  mixins: [chaobject],
  props: {
    cha_check_data: undefined
  },
  data() {
    return {
      pass_action_iems: [
        {
          type: 'verify_pass',
          name: "审核通过 -- 奖励100金币",
          reward_gold: 100
        }, {
          type: 'verify_pass',
          name: "审核通过 -- 奖励50金币",
          reward_gold: 50
        }, {
          type: 'verify_pass',
          name: "审核通过 -- 奖励20金币",
          reward_gold: 20
        },
      ],
      action_items: [
        {
          type: 'verify_fail',
          name: "分类不正确（比如洗浴按摩归于兼职楼凤）"
        },
        {
          type: 'verify_fail',
          name: "联系方式有误"
        },
        {
          type: 'verify_fail',
          name: "虚假图片(会所请上传真实的图片)"
        },
        {
          type: 'verify_fail',
          name: "图片不清晰"
        }, {
          type: 'verify_fail',
          name: "无图片，请上传图片确定真实性"
        },
        {
          type: 'verify_fail',
          name: "信息重复"
        },
        {
          type: 'verify_fail',
          name: "虚假信息"
        },
        {
          type: 'verify_fail',
          name: "涉嫌广告"
        },
        {
          type: 'block_user',
          name: "封禁用户，并将联系方式拉入黑名单，将所有已发布的帖子和未发布的帖子删除"
        }
      ]
    };
  },
  methods: {
    on_action_event: function (action) {
      console.log("on_action_event:", action);
      var post_data = {
        'action': action,
        'id': this.cha_check_data._id
      }
      this.$axios.post('/app/xx/verify/release', post_data).then((response) => {
        this.$message("操作成功");
        this.$emit('on_delete', this.cha_check_data);
        this.cha_check_data = undefined;
      }).catch((error) => {
        console.log("error:", error);
        this.$messageX('操作失败');
      });
    }
  },
  computed: {
    image_list: function () {
      var tmp_image_list = []
      let upload_image_list = this.cha_check_data.release_data.upload_image_list;
      if (upload_image_list && upload_image_list.length > 0) {
        for (var i = 0; i < upload_image_list.length; ++i) {
          tmp_image_list.push(this.generate_tmpimage_url(upload_image_list[i]));
        }
      }
      return tmp_image_list;
    }
  }
}
